import { DietService } from './../../services/diet.service';
import { PlansService } from 'src/app/services/plans.service';
import { UserService } from './../../services/user.service';
import { SnackService } from './../../services/snack.service';
import { Router, ActivatedRoute, NavigationExtras } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-snack-week',
  templateUrl: './snack-week.page.html',
  styleUrls: ['./snack-week.page.scss'],
})
export class SnackWeekPage implements OnInit {
  refeicoes;
  plan: any = '';
  mesAtual: any = '';
  diaSemana;
  dia;
  printCafe:   boolean = false;
  printAlmoco: boolean = false;
  printLanche: boolean = false;
  printJantar: boolean = false;

  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private snackSrv: SnackService,
    private userSrv: UserService,
    private planSrv: PlansService,
    private dietSrv: DietService,
  ) {}

  ngOnInit() {
    this.route.queryParams.subscribe((data) => {
      this.diaSemana = this.getDayOfWeek(data.dia);
      this.dia = data.dia;
    });

    this.loadSnack();
    this.printRefeicao();
  }

  goToDiet(refeicao: number) {
    this.route.queryParams.subscribe((data) => {
      const navigationExtras: NavigationExtras = {
        queryParams: {
          dia: data.dia,
          refeicao: refeicao,
        },
      };
      this.router.navigate(['diet-details'], navigationExtras);
    });
  }

  backToWeekDiet() {
    this.router.navigate(['week-diet']);
  }

  loadSnack() {
    this.snackSrv.getSnacks().subscribe((data) => {
      this.refeicoes = data;
    });

    this.loadProgramUser();
  }

  loadProgramUser() {
    this.userSrv.getUserPlan().subscribe((data) => {
      this.plan = data.programa;
    });

    this.planSrv.getMonthPlan().subscribe((data) => {
      this.mesAtual = data.mes_atual;
    });
  }

  getDayOfWeek(day: number) {
    const diasSemana = ['Domingo', 'Segunda', 'Terça', 'Quarta', 'Quinta', 'Sexta', 'Sábado'];
    return day >= 1 && day <= 7 ? diasSemana[day - 1] : 'Dia inválido';
  }

  printRefeicao() {
    const dataAtual = new Date();
    const horaAtual = dataAtual.getHours();

    if (horaAtual === 8) {
      this.printCafe = true;
    }
    if (horaAtual === 12) {
      this.printAlmoco = true;
    }
    if (horaAtual === 16) {
      this.printLanche = true;
    }
    if (horaAtual === 20) {
      this.printJantar = true;
    }
  }
}
